import React, { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { format, parseISO, intervalToDuration } from 'date-fns';
import API from '../../../../Services';
import Icon from '../../../../components/icon/Icon';
import PageWrapper from '../../../../layout/PageWrapper/PageWrapper';
import Header from '../../../../components/Header';
import {
	DropdownItem,
	DropdownMenu,
	DropdownToggle,
} from '../../../../components/bootstrap/Dropdown';
import Modal, {
	ModalBody,
	ModalFooter,
	ModalHeader,
	ModalTitle,
} from '../../../../components/bootstrap/Modal';
import Dropdown from '../../../../components/bootstrap/Dropdown';
import Button from '../../../../components/bootstrap/Button';
import { cirurgicalFlow } from '../../../../menu';
import Alert from '../../../../components/bootstrap/Alert';
import Page from '../../../../layout/Page/Page';
import Card, { CardBody } from '../../../../components/bootstrap/Card';
import styles from './index.module.css';

const Quadroinformativo = () => {
	const location = useLocation();
	const navigate = useNavigate();

	const [EntradaRPA, setEntradaRPA] = useState(null);
	const [tempoDecorrido, setTempoDecorrido] = useState(null);
	const [timeReload, settimeReloadReload] = useState(Date.now());
	const [dadosPaciente, setDadosPaciente] = useState([]);
	const [modalAlergia, setModalAlergia] = useState(null);
	const [listAlergias, setListAlergias] = useState([]);
	const [modalAlta, setModalAlta] = useState(false);
	const [modalConfirmAlta, setModalConfirmAlta] = useState(false);
	const [modalDesfazerPaciente, setModalDesfazerPaciente] = useState(false);
	const [listMotivosAlta, setListMotivosAlta] = useState([]);
	const [modalConfirmAviso, setmodalConfirmAviso] = useState(false);
	const [biopsia, setBiopsia] = useState(null);

	const [tipoRetornoPaciente, setTipoRetornoPaciente] = useState({
		TIPO: '',
		CODIGO: '',
	});
	const [infoAnestesia, setInfoAnestesia] = useState({
		ANESTESISTA: '',
		ANESTESIA: '',
	});
	const [opcaoSelecionada, setOpcaoSelecionada] = useState({
		CODIGO: null,
		OPCAO: null,
	});

	// Responsável em guardar a opção de alta selecionada pelo colaborador
	const SelecionarOpcao = (codigo, motivo) => {
		setOpcaoSelecionada({
			CODIGO: codigo,
			OPCAO: motivo,
		});
	};

	// Responsável em retornar todas as alergias do paciente que está no quadro.
	const retornaAlergiasPaciente = async (codAvisoCirurgia) => {
		try {
			const request = await API.get(`/retornaAlergiasPaciente/${codAvisoCirurgia}`);
			setListAlergias(request.data);
		} catch (error) {
			console.log(error);
		}
	};

	// Retorna Motivos Alta
	const retornaMotivosAlta = async () => {
		try {
			const request = await API.get(`/retornaMotivosAlta`);
			setListMotivosAlta(request.data);
		} catch (error) {
			console.log(error);
		}
	};

	const alterarLocalidade = () => {
		localStorage.removeItem('CAPP-local');
		navigate('/');
	};

	useEffect(() => {
		if (!localStorage.getItem('CAPP-local')) {
			navigate('/');
		}

		// Retorna todas as informações do Paciente para implementar no quadro de informações.
		const retornaDadosQuadroInducao = async () => {
			const session = JSON.parse(localStorage.getItem('CAPP-local'));

			try {
				const patient = await API.get(`/retornaQuadroRPA/${session.CODIGO_SUBLOCALIDADE}`);
				const response = await patient.data;
				if (!response.length) {
					navigate('/');
					return;
				}

				if (patient) {
					setDadosPaciente(response[0]);

					// recupera informações referente à anestesia
					const requestAnestesia = await API.get(
						`/retornaAnestesiaPaciente/${response[0].CD_ATENDIMENTO}`,
					);
					const requestAnestesista = await API.get(
						`/retornaAnestesistaPaciente/${response[0].CD_ATENDIMENTO}`,
					);

					setInfoAnestesia({
						ANESTESISTA: requestAnestesista.data[0].ANESTESISTA,
						ANESTESIA: requestAnestesia.data[0].ANESTESIA,
					});

					// consulta biópsia do Paciente

					const requestBiopsia = await API.get(
						`/retornaBiopsiaPaciente/${response[0].CD_ATENDIMENTO}`,
					);
					setBiopsia(requestBiopsia.data[0].BIOPSIA);

					const initialParsedDateBed = parseISO(patient.data[0].ENTRADA);

					const initialDateBedLocalStorageFormated = format(
						initialParsedDateBed,
						'HH:mm',
					);
					console.log(`Data de entrada formatada: ${initialParsedDateBed}`);

					const bedInterval = intervalToDuration({
						start: new Date(initialParsedDateBed),
						end: new Date(
							format(new Date(Date.now()), 'yyyy'),
							format(new Date(Date.now()), 'M') - 1,
							format(new Date(Date.now()), 'd'),
							format(new Date(Date.now()), 'HH'),
							format(new Date(Date.now()), 'mm'),
							0,
						),
					});

					const bedDays = JSON.stringify(bedInterval.days);
					const bedHours = JSON.stringify(bedInterval.hours);
					const bedMinutes = JSON.stringify(bedInterval.minutes);

					setEntradaRPA(initialDateBedLocalStorageFormated);

					let bedDaysToHours = null;
					if (bedDays > 0) {
						bedDaysToHours = bedDays * 24;
					} else {
						bedDaysToHours = bedHours;
					}

					const bInterval = `${bedDaysToHours}:${
						bedMinutes < 10 ? 0 + bedMinutes : bedMinutes
					} `;

					setTempoDecorrido(bInterval);
				}
			} catch (error) {
				console.log(error);
			}
		};

		console.log(location.state);

		retornaDadosQuadroInducao();
		retornaMotivosAlta().catch(console.error);

		const interval = setInterval(() => settimeReloadReload(Date.now()), 60000);
		return () => {
			clearInterval(interval);
		};

		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [navigate, timeReload, location, dadosPaciente.COD_AVISO]);

	const sessionQ = JSON.parse(localStorage.getItem('CAPP-local'));
	const localidadeHeader = `SRPA L${sessionQ.SUBLOCALIDADE.split(' ')[1]}`;

	return (
		<PageWrapper title={cirurgicalFlow.RPA_QUADRO_INFO.text}>
			<Header titulo={localidadeHeader} localidade='Quadro Informativo'>
				<Dropdown>
					<DropdownToggle>
						<Button id='menu' color='light' icon='menu' isLight>
							<p> </p>
						</Button>
					</DropdownToggle>
					<DropdownMenu>
						<DropdownItem onClick={() => alterarLocalidade()}>
							Alterar Local
						</DropdownItem>
						{
							// Só permite que a opção seja carregada, quando os dados do paciente for carregado.
							dadosPaciente.PACIENTE && (
								<DropdownItem
									onClick={() => {
										document.getElementById('menu').click();
										setmodalConfirmAviso(true);
									}}>
									Desfazer Etapa do Paciente
								</DropdownItem>
							)
						}
					</DropdownMenu>
				</Dropdown>
			</Header>
			<Page>

				{dadosPaciente.PACIENTE && (
					<Card className={`${styles.bloco_informacoes} ${styles.pos_info_paciente}`}>
						<CardBody className={styles.card_info_rpa}>
							<div className={styles.informacoes}>
								<div className={styles.info_wrapper}>
									<h2 className={styles.paciente_nome_titulo}>Paciente</h2>
									<span className={styles.paciente_nome}>
										{dadosPaciente.PACIENTE}{' '}
									</span>
								</div>
							</div>
							<div className={styles.sub_informacoes}>
								<div className={styles.paciente_data_de_nascimento}>
									<div className={styles.info_wrapper}>
										<h2 className={styles.paciente_data_titulo}>
											Data de Nascimento
										</h2>
										<span className={styles.paciente_data}>
											{dadosPaciente.DT_NASCIMENTO}
										</span>
									</div>
								</div>
								<div className={styles.paciente_procedimento}>
									<div className={styles.info_wrapper}>
										<h2 className={styles.procedimento_titulo}>Procedimento</h2>
										<span className={styles.paciente_procedimento}>
											{dadosPaciente.PROCEDIMENTO_PRINCIPAL}
										</span>
									</div>
								</div>
							</div>
						</CardBody>
					</Card>
				)}
				{dadosPaciente.PACIENTE ? (
					<div className={styles.informations}>
						<Card shadow='lg' className={styles.card_entrada}>
							<br />
							<CardBody className={styles.card_entrada_body}>
								<Icon
									icon='bedroom_child'
									color='info'
									size='7x'
									forceFamily='material'
								/>
								<br />
								<h1 className={styles.tempo_decorrido_rpa}>{tempoDecorrido}</h1>
								<h2 className={styles.tempo_entrada_rpa}>
									({EntradaRPA}h)
									<h4>Hora Entrada</h4>
								</h2>
							</CardBody>
						</Card>

						<div className={styles.others_info}>
							<Card shadow='lg' className={styles.card_anestesista}>
								<CardBody className={styles.card_anestesista_body}>
									<h3>
										{infoAnestesia.ANESTESIA === null ? (
											<span style={{ color: '#FE5B3A' }}>
												Anestesia não informada
											</span>
										) : (
											infoAnestesia.ANESTESIA
										)}
									</h3>
									<h2>
										<strong>
											{infoAnestesia.ANESTESISTA === null ? (
												<span style={{ color: '#FE5B3A' }}>
													Anestesista não informado
												</span>
											) : (
												infoAnestesia.ANESTESISTA
											)}
										</strong>
									</h2>
								</CardBody>
							</Card>

							<div className={styles.options}>
								<Card
									shadow='lg'
									className={`${styles.card_alergia} ${styles.ca_position}`}
									onClick={() => {
										// eslint-disable-next-line no-unused-expressions
										dadosPaciente.ALERGIA === 'NAO'
											? console.log('Não há alergias.')
											: setModalAlergia(true);
										retornaAlergiasPaciente(dadosPaciente.COD_AVISO);
									}}>
									<CardBody className={styles.card_alergia_body}>
										<h4>ALERGIA</h4>
										{dadosPaciente.ALERGIA === 'NAO' ? (
											<h1 className={styles.texto_stilo_nao}>NÃO</h1>
										) : (
											<h1 className={styles.texto_stilo_sim}>SIM</h1>
										)}
									</CardBody>
								</Card>
								<Card
									shadow='lg'
									className={`${styles.card_alergia} ${styles.ca_position}`}>
									<CardBody className={styles.card_alergia_body}>
										<h4>BIÓPSIA</h4>
										{
											// eslint-disable-next-line no-nested-ternary
											biopsia === 'N' ? (
												<h1 className={styles.texto_stilo_nao}>NÃO</h1>
											) : biopsia === null ? (
												<h1 className={styles.texto_stilo_nao}>NÃO</h1>
											) : (
												<h1 className={styles.texto_stilo_sim}>SIM</h1>
											)
										}
									</CardBody>
								</Card>
							</div>
						</div>
					</div>
				) : (
					<div className={styles.lds_ring}>
						<div />
						<div />
						<div />
						<div />
					</div>
				)}

				<Modal
					isCentered='true'
					isAnimation={false}
					id='modalListaAlergias'
					isOpen={modalAlergia}>
					<ModalHeader className={styles.modal_header_alergias_list}>
						<ModalTitle>
							ALERGIAS{' '}
							<span className='badge bg-l10-info text-info shadow-lg rounded'>
								{listAlergias.length}
							</span>
						</ModalTitle>
					</ModalHeader>
					<ModalBody>
						<ul className='list-group'>
							{listAlergias.ALERGIA !== 'null' ? (
								listAlergias.map((i) => {
									return <li className='list-group-item'>{i.ALERGIA}</li>;
								})
							) : (
								<h3>Não há cirurgias</h3>
							)}
						</ul>
					</ModalBody>
					<ModalFooter>
						<Button
							size='lg'
							className='btn-style'
							color='light'
							onClick={() => {
								setModalAlergia(false);
								setListAlergias([]);
							}}>
							Fechar
						</Button>
					</ModalFooter>
				</Modal>
				<Modal
					isCentered='true'
					isAnimation={false}
					id='modalConfirmAlta'
					isOpen={modalConfirmAlta}>
					<ModalHeader style={{ backgroundColor: '#FFCF64' }}>
						<ModalTitle style={{ color: '#fff' }}>CONFIRMAÇÃO</ModalTitle>
					</ModalHeader>
					<ModalBody className='ModalBody'>
						<h3>
							Confirma o motivo da alta:
							<br />
							<Icon icon='AssignmentTurnedIn' size='8px' color='success' />{' '}
							<strong>{opcaoSelecionada.OPCAO}</strong>?
						</h3>
					</ModalBody>
					<ModalFooter>
						<Button
							size='lg'
							color='light'
							onClick={() => {
								setModalConfirmAlta(false);
								setModalAlta(true);
							}}>
							NÃO
						</Button>
						<Button
							size='lg'
							color='success'
							onClick={() => {
								navigate('/rpa/assinaturaAcoes', {
									state: {
										TP_ACAO: 'AVANCAR_ETAPA',
										COD_AVISO: dadosPaciente.COD_AVISO,
										DADOS: { DADOS: dadosPaciente },
										ETAPA_ALTA_ESCOLHIDA: opcaoSelecionada.CODIGO,
									},
								});
							}}>
							SIM
						</Button>
					</ModalFooter>
				</Modal>

				<Modal
					isCentered='true'
					isAnimation={false}
					id='modalConfirmAviso'
					isOpen={modalConfirmAviso}>
					<ModalHeader style={{ backgroundColor: 'rgb(220 178 85)' }}>
						<ModalTitle style={{ color: '#fff' }}>DESFAZER ETAPA</ModalTitle>
					</ModalHeader>
					<ModalBody className='ModalBody'>
						<br />
						<h3>RETORNAR O PACIENTE À S.O:</h3>
						<Alert
							icon='Info'
							color='light'
							onClick={() => {
								setTipoRetornoPaciente({
									CODIGO: 'M',
									TIPO: 'MESMO CÓDIGO DE AVISO',
								});
								setmodalConfirmAviso(false);
								setModalDesfazerPaciente(true);
							}}>
							<strong>MESMO AVISO</strong>
						</Alert>

						<Alert
							icon='Info'
							color='light'
							onClick={() => {
								setTipoRetornoPaciente({
									CODIGO: 'O',
									TIPO: 'OUTRO CÓDIGO DE AVISO',
								});
								setmodalConfirmAviso(false);
								setModalDesfazerPaciente(true);
							}}>
							<strong>OUTRO AVISO</strong>
						</Alert>
						<br />
						<h3>DESFAZER ETAPA:</h3>
						<Alert
							icon='Info'
							color='light'
							onClick={() => {
								setTipoRetornoPaciente({ CODIGO: 'D', TIPO: 'ERRO OPERACIONAL' });
								setmodalConfirmAviso(false);
								setModalDesfazerPaciente(true);
							}}>
							<strong>ERRO OPERACIONAL</strong>
						</Alert>
						<p className='text-muted'>O paciente retornará a última etapa informada.</p>
					</ModalBody>
					<ModalFooter>
						<Button
							size='lg'
							className='btn-style'
							color='light'
							onClick={() => {
								setmodalConfirmAviso(false);
							}}>
							CANCELAR
						</Button>
					</ModalFooter>
				</Modal>

				<Modal
					isCentered='true'
					isAnimation={false}
					id='modalDesfazerPaciente'
					isOpen={modalDesfazerPaciente}>
					<ModalHeader style={{ backgroundColor: 'rgb(220 178 85)' }}>
						<ModalTitle style={{ color: '#fff' }}>DESFAZER ETAPA</ModalTitle>
					</ModalHeader>
					<ModalBody>
						<h2>Confirme as informações abaixo:</h2>
						<br />
						<div>
							<h3>Paciente:</h3>
							<h4 style={{ color: '#9e9e9e' }}>{dadosPaciente.PACIENTE}</h4>
							<br />
							<br />
							<h3>Tipo:</h3>
							<h4 style={{ color: '#9e9e9e' }}>{tipoRetornoPaciente.TIPO}</h4>
							<br />
						</div>
					</ModalBody>
					<ModalFooter>
						<Button
							size='lg'
							color='light'
							onClick={() => {
								setModalDesfazerPaciente(false);
								setmodalConfirmAviso(true);
							}}>
							VOLTAR
						</Button>
						<Button
							size='lg'
							color='success'
							onClick={() => {
								navigate('/rpa/assinaturaAcoes', {
									state: {
										TP_ACAO: 'RETORNAR_PACIENTE',
										CRITERIO: {
											TIPO: tipoRetornoPaciente.TIPO,
											CODIGO: tipoRetornoPaciente.CODIGO,
										},
										COD_AVISO: dadosPaciente.COD_AVISO,
										DADOS: { DADOS: dadosPaciente },
									},
								});
							}}>
							CONFIRMAR
						</Button>
					</ModalFooter>
				</Modal>

				<Modal isCentered='true' isAnimation={false} id='modalAlta' isOpen={modalAlta}>
					<ModalHeader style={{ backgroundColor: '#4D4F53' }}>
						<ModalTitle style={{ color: '#fff' }}>
							INFORME ABAIXO O MOTIVO DA ALTA
						</ModalTitle>
					</ModalHeader>
					<ModalBody>
						{listMotivosAlta.map((i) => {
							return (
								<Alert
									className={styles.bloco_alta}
									key={i.CODIGO_MV}
									rounded='0'
									isLight
									color='light'
									style={{ fontSize: '25px', cursor: 'pointer' }}
									onClick={() => {
										SelecionarOpcao(i.CODIGO_MV, `${i.ETAPA}`);
										setModalAlta(false);
										setModalConfirmAlta(true);
									}}>
									{i.ETAPA}
								</Alert>
							);
						})}
					</ModalBody>
					<ModalFooter>
						<Button
							color='light'
							onClick={() => {
								setModalAlta(false);
							}}>
							FECHAR
						</Button>
					</ModalFooter>
				</Modal>

				<div className={styles.button_area}>
					{dadosPaciente.PACIENTE && (
						<Button
							onClick={() => setModalAlta(true)}
							className={styles.button_alta}
							color='dark'
							size='lg'>
							<h1>
								Alta
								<Icon icon='login' color='#fff' size='3x' forceFamily='material' />
							</h1>
						</Button>
					)}
				</div>
			</Page>
		</PageWrapper>
	);
};

export default Quadroinformativo;